@import 'components/variables';
@import 'components/global';

.section-tabs {
  background-color: $white;
  padding-top: calc(135px + 1rem);
  padding-bottom: 5rem;
  @media (min-width: 1200px) {
    padding-top: calc(135px + 4rem);
  }
  .nav-pills {
    padding: 2rem 0;
    background-image: url('../images/bg-blue.png');
    background-size: cover;
    background-position: bottom center;
    display: flex;
    justify-content: space-evenly;
    .nav-link {
      color: $white;
      border-bottom: 1.5px solid transparent;
      border-radius: 0px;
      &.active {
        background-color: transparent;
        border-bottom: 1.5px solid $yellow;
        color: $yellow;
      }
      &:hover {
        color: $yellow;
      }
    }
  }
  .tab-pane {
    position: relative;
    margin-top: 5rem;
    .content-tab {
      background-color: $gray-light;
      border-radius: 25px;
    }
    .content-img {
      img  {
        border-top-left-radius: 25px;
        border-top-right-radius: 25px;
      }
    }
    .btn-categoria {
      border-radius: 50%;
      background: linear-gradient(180deg, rgba(247,208,84,1) 0%, rgba(251,131,27,1) 100%);
      width: 80px;
      height: 80px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      top: 0;
      right: 0;
      transform: translateX(10%) translateY(-35%);
      @media (min-width: 768px) {
        width: 100px;
        height: 100px;
        transform: translateY(-25%);
      }
      .categoria {
        color: $white;
        margin-bottom: 0;
        font-weight: 400;
        font-size: 1rem;
      }
    }
    .compartir-redes {
      background-color: $gray-light;
      border-radius: 50px;
      display: flex;
      align-items: center;
      ul {
        display: flex;
        align-items: center;
        list-style: none;
        margin-bottom: 0;
        li {
          transition: all .3s;
          padding: 0 1rem;
          cursor: pointer;
          &:hover {
            transform: scale(0.8);
          }
        }
      }
    }
  }
}

.section-articulos-relac {
  padding-bottom: 3rem;
  .text-articulos {
    color: $blue;
    font-size: 1.4rem;
    font-weight: bold;
    margin-bottom: 0;
  }
  .articulos-relacionados {
    .slick-slide {
      margin: 0 1rem;
    }
    .slide {
      position: relative;
      padding: 0 1rem;
      border-radius: 25px;
      transition: all .3s;
      &:hover {
        background-color: $gray-light;
        .content-slide .read-more {
          color: $yellow;
        }
        .btn-categoria {
          opacity: 1;
        }
      }
      a {
        text-decoration: none;
        &:focus {
          outline: none;
        }
      }
      .btn-categoria {
        border-radius: 50%;
        background: linear-gradient(180deg, rgba(247,208,84,1) 0%, rgba(251,131,27,1) 100%);
        width: 70px;
        height: 70px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 0;    
        left: 0;
        transform: translateX(20%) translateY(20%);
        opacity: 0;
        transition: all .3s;
        .categoria {
          color: $white;
          margin-bottom: 0;
          font-weight: 400;
          font-size: .7rem;
        }
      }
      .content-slide {
        display: flex;
        flex-direction: column;
        justify-content: center;
        .text-slide {
          color: $blue;
          font-size: 1.2rem;
          font-weight: bold;
          margin-bottom: 0;
        }
        .descrip-slide {
          color: $gray;
          font-size: .8rem;
          margin-bottom: 0;
        }
        .read-more {
          color: $blue;
          font-size: .8rem;
          margin-bottom: 0;
          margin-top: .5rem;
          transition: all .3s;
        }
      }
    }
    .slick-next {
      width: 40px;
      right: -40px;
      transform: translateY(-100%);
      opacity: 0.4;
      transition: all .3s;
      &:hover {
        opacity: 1;
      }
    }
    .slick-prev {
      left: -40px;
      width: 40px;
      z-index: 1;
      transform: translateY(-100%);
      opacity: 0.4;
      transition: all .3s;
      &:hover {
        opacity: 1;
      }
    }
    .slick-next:before,
    .slick-prev:before {
      display: none;
    }
  }
  .btn-cargar {
    color: $white;
    font-weight: 400;
    font-size: 1rem;
    &:hover {
      color: $blue;
    }
  }
}

.section-dudas {
  background-image: url('../images/int1/bg-dudas.png');
  background-size: cover;
  background-position: top center;
  padding-top: 10rem;
  padding-bottom: 5rem;
  @media (min-width: 1700px) {
    padding-top: 13rem;
  }
  .text-dudas-yellow {
    color: $yellow;
    font-size: 2rem;
    font-weight: bold;
  }
  .text-dudas-white {
    color: $white;
    font-size: 2rem;
    font-weight: 300;
    line-height: 1;
  }
  .btn-cargar {
    color: $white;
    font-weight: 400;
    font-size: 1rem;
    &:hover {
      color: $blue;
    }
  }
  .icon-whatsapp {
    transition: all .3s;
    &:hover {
      transform: scale(1.1);
    }
  }
}
