html, body {
  font-family: 'Poppins', sans-serif;
  overflow-x: hidden;
  @media (max-width: 424.98px) {
    font-size: 12px;
  }
  @media (max-width: 359.98px) {
    font-size: 10px;
  }
}
.navbar {
  .btn-custom {
    width: 130px;
  }
}
.text-parrafos {
  color: $gray;
  margin-bottom: 0;
}
.text-gray-light {
  color: #c5bdbd;
  margin-bottom: 0;
}
.text-blue {
  color: $blue;
  font-weight: 500;
}
.title {
  color: $blue;
  font-weight: bold;
  margin-bottom: 1rem;
  font-size: 2rem;
  span {
    color: $yellow;
  }
}
.title-blanco {
  color: $white;
  font-size: 2rem;
  font-weight: 200;
  margin-bottom: 0;
  span {
    color: $yellow;
    font-weight: bold;
  }
}
#redes {
  display: flex;
  a {
    img {
      height: 16px;
    }
  }
  .red {
    vertical-align: middle;
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    transition-property: color;
    transition-duration: 0.3s;
    border-radius: 3px;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    &:before {
      border-radius: 3px;
      width: 30px;
      height: 30px;
      content: "";
      position: absolute;
      z-index: -1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      transform: scaleY(0);
      transform-origin: 50% 100%;
      transition-property: transform;
      transition-duration: 0.3s;
      transition-timing-function: ease-out;
    }
    &.fb:before {
      background-color: #3b5998;
    }
    &.ig:before {
      background: linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #bc1888 75%, #833ab4 100%) !important;
    }
    &.fb:hover:before,
    &.ig:hover:before {
      transform: scaleY(1);
    }
  }
}
